import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import action from '../../components/shufei-action/action.vue';
export default {
	components: {
		uniList,
		uniListItem,
		action
	},
	data() {
		return {
			list: [
				{
					title: '我的银行卡',
					themb: '../../static/other/yue_card.png',
					url: 'wallet'
				},
				{
					title: '待结算金额',
					themb: '../../static/other/yue_tixian.png',
					url: 'people'
				}
			],
			type: ''
		};
	},
	methods: {
		onNavigationBarButtonTap(e) {
			console.log(e.index);
			uni.navigateTo({ url: '/pages/ohter/balance' });
		},
		goDetailPage(url) {
			uni.navigateTo({ url: '/pages/ohter/' + url + '' });
		},

		itemClick(index, type) {
			//这里根据不同的类型点击的索引值,做对应的逻辑处理
			console.log(index)
			uni.navigateTo({
				url: `/pages/ohter/withdraw?type=${index}`
			});
		},

		openType0() {
			this.$refs.action.actionConfig = {
				title: '',
				list: ['银行卡', '支付宝'],
				type: 0,
				isBorderColor: false,
				specClass: 'show'
			};
		},
		terminate() {
			uni.navigateTo({
				url: 'reCharge'
			});
		}
	}
};
